//这边基本上引入需要使用的资源less，api，需要使用的模块等等。

class ytjtaskdetail extends Interstellar.pagesBase {
    complete() {
        this.styleModel(1)
        this.type = this.app.parpam.type
        this.taskId = this.app.parpam.taskid * 1
        this.taskType = this.app.parpam.taskType
        console.log(this.app.model.get('ytjtaskdetail'), 'xxx')
        this.model._event._addEvent('taskid.change', () => {
            this.settaskdata()
        })
        this.action = {
            viewmark: {
                dis: 'inline',
                link: 'noLink',
                content: '查看标注结果'
            }
        }
        this.action1 = {
            viewseries: {
                dis: 'inline',
                link: 'noLink',
                content: '查看序列'
            }
        }
        this.render();
        this.resize()
        this.setTitle()
        this.task_read()
        this.dom.find('.breadcrumb .lastlink').on('click', () => {
            this.app.model.del('ytjtaskdetail')
            this.app.changePage(this.taskType == 1 ? 'taskmanage' : 'backflowtask', {
                type: 'fs'
            })
        })
        this.model._event._addEvent('querylist.change', async() => {
            let temp = this.model.getData('querylist').reset
            this.app.loading.show()
            let res = await this.api.getuserseries(this.model.getData('querylist'))
            this.app.loading.hide()
            if (res.code == 0) {
                this.dom.find('label[api="seriesEndNum"]').html(parseInt(res.data.total) - parseInt(this.doingnum))
                this.setMain(res, temp);
            }
        })
    }

    render() {
        require.ensure("../moduleslibs/menutree/menutree.js", () => {
            let menu = require("../moduleslibs/menutree/menutree.js")
            this.menutree = this.app.loadModule(menu, this.dom.find('.personarea .left'), {
                config: ['venderName', 'mobile_phone', 'discardNum', 'workNum']
            })
            this.model.setData('taskid', this.taskId)
            this.menutree.event._addEvent('navFirst.choosed', (value) => {
                this.doingnum = value.config.split(',')[3]
                this.dom.find('label[api="venderName"]').html(value.config.split(',')[0] !== 'null' ? value.config.split(',')[0] : '')
                this.dom.find('label[api="username"]').html(value.config.split(',')[1])
                this.dom.find('label[api="discardNum"]').html(value.config.split(',')[2])
                this.dom.find('label[api="workNum"]').html(value.config.split(',')[3])
                    // this.dom.find('input[api="serialNumber"]').val('')
                this.model.querylist.userId = parseInt(value.id)
                this.model.querylist.serialNumber = this.dom.find('input[api="serialNumber"]').val()
                this.model.setData('querylist', this.model.querylist)
                    // this.model.setData('querylist', {
                    //   taskId: parseInt(this.taskId),
                    //   userId: parseInt(value.id),
                    //   page: 1,
                    //   pageSize: 10,
                    //   reset: true,
                    // })
            })
        })
        this.dom.find('.biaozhubtn').on('click', () => {
            // if(this.seriesImgFileType == 3){
            //   let json={
            //     taskId:this.taskId,
            //     userId:JSON.parse(this.app.local.get('all')).userId,
            //     accessToken:this.app.local.get('accessToken'),
            //     serviceHost:window.location.origin,
            //     type:1
            //   }
            //   console.log(json,Base64.encoder(JSON.stringify(json)))
            //   window.open('annotation://'+Base64.encoder(JSON.stringify(json)))
            //   return
            // }
            this.model.querylist.reset = true
            this.app.model.set('ytjtaskdetail', this.model.querylist)
            this.app.changePage(this.largeFigure ? 'drapCanvasCheck' : 'markview', {
                    taskId: this.app.parpam['taskid'],
                    projectId: this.projectId,
                    type: 'viewer_all',
                    uid: this.model.querylist.userId,
                    sid: Base64.encode('1'),
                    taskType: this.taskType
                })
                // let page = this.largeFigure ? 'drapCanvasCheck':'markview'
                // let url = 'http://' + window.location.host + '/#!/'+page+'/' + this.app.parpam['taskid'] + '/' + this.projectId + '/viewer_all/' + this.model.querylist.userId + '/1//'+this.taskType
                // let link = document.createElement('a')
                // link.target = "_blank";
                // link.href = url;
                // document.body.appendChild(link);
                // link.click();
                // document.body.removeChild(link);
        })
        this.dom.find('.taskdetail .export').on('click', () => {
            let url = this.app.domain1 + 'v1/task/series/discard/export/?taskId=' + this.taskId;
            Tool.downfile(url, this.app)
        })
        this.dom.find('.sequencearea .search').on('click', () => {
            this.model.setData('querylist', {
                taskId: parseInt(this.taskId),
                userId: parseInt(this.dom.find('.choosedks').attr('did')),
                page: 1,
                pageSize: 10,
                reset: true,
                serialNumber: this.dom.find('input[api="serialNumber"]').val()
            })
        })
    }

    setTitle() {
        let obj = {}
        obj['icon'] = {
            "serialNumber": {
                name: '<span data-i18n="date" data-name="检查时间">序号</span>',
                type: 'text',
                code: 'date',
                w: '55%',
                ww: '55%',
                n: 40
            },
            "discarded": {
                name: '<span data-i18n="action" data-name="操作">是否废片</span>',
                type: 'text',
                code: 'action',
                w: '10%',
                ww: '10%'
            },
            "status": {
                name: '<span data-i18n="action" data-name="操作">标注状态</span>',
                type: 'text',
                code: 'action',
                w: '10%',
                ww: '10%'
            },
            "submitTime": {
                name: '<span data-i18n="action" data-name="操作">提交时间</span>',
                type: 'text',
                code: 'action',
                w: '25%',
                ww: '25%'
            }
        };
        obj['actionulwidth'] = 200
        obj['tablewidth'] = ES.selctorDoc('.ytjtaskdetail').box().clientWidth - 471;
        obj['type'] = 'center';
        obj['actionicon'] = {
            "operation": {
                name: '<span data-i18n="action" data-name="操作">操作</span>',
                type: 'action',
                code: 'action',
                w: '100%',
                ww: '100%'
            }
        };
        require.ensure("../moduleslibs/table_group/table_group", () => {
            let cont_table = require("../moduleslibs/table_group/table_group")
            this.table = this.app.loadModule(cont_table, this.dom.find('.serieslist'), {
                id: 'serieslist',
                header: obj
            })
            this.table.event._addEvent('table.action', (value) => {
                let page = this.largeFigure ? 'drapCanvasCheck' : 'markview'
                    // if (this.seriesImgFileType == 3) {
                    //   let json={
                    //     taskId:this.app.parpam['taskId']*1,
                    //     userId:JSON.parse(this.app.local.get('all')).userId,
                    //     accessToken:this.app.local.get('accessToken'),
                    //     serviceHost:window.location.origin,
                    //     serialNumber:value.id,
                    //     type:2
                    //   }
                    //   window.open('annotation://'+Base64.encoder(JSON.stringify(json)))
                    //   return
                    // }
                if (value.classname == 'viewmark') {
                    // let url = 'http://' + window.location.host + '/#!/'+page+'/' + this.app.parpam['taskid'] + '/' + this.projectId + '/viewer_admin/' + this.model.querylist.userId + '/' + value.id+'//'+this.taskType
                    // let link = document.createElement('a');
                    // link.target = "_blank";
                    // link.href = url;
                    // document.body.appendChild(link);
                    // link.click();
                    // document.body.removeChild(link);
                    this.model.querylist.reset = true
                    this.app.model.set('ytjtaskdetail', this.model.querylist)
                    this.app.changePage(this.largeFigure ? 'drapCanvasCheck' : 'markview', {
                        taskId: this.app.parpam['taskid'],
                        projectId: this.projectId,
                        type: 'viewer_admin',
                        uid: this.model.querylist.userId,
                        sid: Base64.encode(value.id),
                        taskType: this.taskType
                    })

                } else if (value.classname == 'viewseries') {
                    // let url = 'http://' + window.location.host + '/#!/'+page+'/' + this.app.parpam['taskid'] + '/' + this.projectId + '/viewer_admin/' + this.model.querylist.userId + '/' + value.id+'//'+this.taskType
                    // let link = document.createElement('a');
                    // link.target = "_blank";
                    // link.href = url;
                    // document.body.appendChild(link);
                    // link.click();
                    // document.body.removeChild(link);
                    this.model.querylist.reset = true
                    this.app.model.set('ytjtaskdetail', this.model.querylist)
                    this.app.changePage(this.largeFigure ? 'drapCanvasCheck' : 'markview', {
                        taskId: this.app.parpam['taskid'],
                        projectId: this.projectId,
                        type: 'viewer_admin',
                        uid: this.model.querylist.userId,
                        sid: Base64.encode(value.id),
                        taskType: this.taskType
                    })
                }
            });
            this.table.event._addEvent('table.pagenumber', (value) => {
                let json = this.model.getData('querylist')
                json.reset = false
                json.page = parseInt(value);
                this.table.changenum(parseInt(value));
                this.model.setData('querylist', json)
            });
            this.table.event._addEvent('table.pagesize', (value) => {
                let json = this.model.getData('querylist')
                json.page = 1;
                json.pageSize = value.num;
                json.reset = true;
                this.model.setData('querylist', json)
            });
            this.dom.find('.list-content').removeClass('hide');
            this.resize()
        })
    }

    setMain(res, bool) {
        let data2 = []
        if (res.code == 0) {
            if (res.data.list.length > 0) {
                res.data.list.forEach((val) => {
                    let obj = {}
                    val.id = val.serialNumber
                    obj.id = val.id;
                    if (val.discarded == true) {
                        obj.operation = this.action1;
                        val.discarded = '是'
                    } else {
                        obj.operation = this.action;
                        val.discarded = '否'
                    }
                    if (this.studyAnno) {
                        val.imageTotalNum = ''
                    }
                    data2.push(obj)
                    if (String(val.status) == '2' || String(val.status) == '1') {
                        val.status = "标注进行中"
                    } else {
                        val.status = "已提交"
                    }
                    val.submitTime = val.submitTime ? Tool.time(val.submitTime, 'yyyy-mm-dd HH:MM:ss') : '无'
                })
                this.table.setData(res.data.list, data2)
            } else {
                this.table.noData();
            }
        }
        if (bool) {
            this.table.getTotal(res.data.pages, 2, res.data.total)
            this.table.changenum(this.model.querylist.page, this.model.querylist.pageSize);
        }
        this.initscroll();
    }

    async settaskdata() {
        let json = {
            id: this.taskId
        }
        let temp = ['name', 'projectName', 'startTime', 'endTime', 'algPreAnnotation', 'seriesTotalNum', 'seriesSubmittedNum', 'seriesDiscardNum', 'seriesProcessNum', 'discardNum', 'workNum']
        let res = await this.api.gettask(json)
        if (res.code == 0) {
            res.data.startTime = Tool.time(res.data.startTime, 'yyyy-mm-dd')
            res.data.endTime = Tool.time(res.data.endTime, 'yyyy-mm-dd')
            if (res.data.studyAnno) {
                this.studyAnno = true
                res.data.algPreAnnotation = res.data.algPreAnnotation ? '算法标注（以检查号为维度）' : '人工标注（以检查号为维度）'
            } else {
                res.data.algPreAnnotation = res.data.algPreAnnotation ? '算法标注' : '人工标注'
            }
            if (res.data.seriesDiscardNum > 0) {
                this.dom.find('.taskdetail .export').removeClass('hide')
            }
            let num = res.data.crossMarkNum ? res.data.crossMarkNum : 1
            temp.forEach((val, idx) => {
                if (val == 'seriesTotalNum') {
                    let temp = `${res.data[val] / num}(*${num})`;
                    this.dom.find('label[apidata="' + val + '"]').html(temp)
                } else {
                    this.dom.find('label[apidata="' + val + '"]').html(res.data[val])
                }
            })
            this.projectId = res.data.projectId
            this.menutree.changeMenu(res.data.userList, 'single')
            if (this.app.model.get('ytjtaskdetail')) {
                this.model.setData('querylist', this.app.model.get('ytjtaskdetail'))
                if (this.app.model.get('ytjtaskdetail').serialNumber) {
                    console.log(this.dom.find('.filterarea input[api="serialNumber"]').dom, this.app.model.get('ytjtaskdetail').serialNumber)
                    this.dom.find('.filterarea input[api="serialNumber"]').val(this.app.model.get('ytjtaskdetail').serialNumber)
                }
                if (this.app.model.get('ytjtaskdetail').userId) {
                    this.dom.find('.treeArea .navFirst[did="' + this.app.model.get('ytjtaskdetail').userId + '"]').click()
                }
            } else {
                console.log(this.dom.find('.treeArea .navFirst').dom)
                this.dom.find('.treeArea .navFirst').eq(0).click()
            }
            this.menutree.resize(ES.selctorDoc(window).box().clientHeight - 360)
        }
    }
    async task_read() {
        let res = await this.api.task_read({
            id: this.taskId
        })
        console.log(res, 'res')
        if (res.code == 0) {
            this.seriesImgFileType = res.data.seriesImgFileType
            this.largeFigure = res.data.largeFigure
        }
    }
    initscroll() {
        if (this.myScroll) {
            this.myScroll.refresh()
            return
        }
        var rid = 'aaa_' + Math.floor(new Date().getTime() * Math.random())
        this.dom.find('.serieslist .scrolltable').attr('id', rid)
        this.myScroll = new IScroll('#' + rid, {
            scrollbars: true,
            mouseWheel: true,
            interactiveScrollbars: true,
            hideScrollbar: false,
            vScrollbar: true,
            shrinkScrollbars: 'scale',
            fadeScrollbars: false,
            disableMouse: true,
            disablePointer: true
        });
    }

    resize() {
        let ch = ES.selctorDoc(window).box().clientHeight
        let cw = ES.selctorDoc(window).box().clientWidth - 40
        ES.selctorDoc('.ytjtaskdetail').css({
            'height': ch - 100,
            'width': cw
        });
        this.dom.find('.personarea').css({
            'height': ch - 360
        })
        this.dom.find('.listarea').css({
            'width': cw - 231
        })
        this.dom.find('.scrolltable').css({
            'height': ch - 510,
            'width': cw - 231
        })
    }

}

module.exports = ytjtaskdetail;


